import React from "react";
import { useState, useContext, useEffect } from "react";
import { Link } from "react-router-dom";

import "./MyPage.css";
import { FetchUrl } from "../../store/communication";

import userInfor from "../json/member.json";

function MyPage() {
  const FETCH_URL = useContext(FetchUrl);

  const [userInformation, setUserInforMation] = useState(
    userInfor.responseData
  );

  const url = `${FETCH_URL}/members`;

  function getCookie(name) {
    const cookie = document.cookie
      .split(";")
      .map((cookie) => cookie.split("="))
      .filter((cookie) => cookie[0] === name);
    return cookie[0][1];
  }
  useEffect(() => {
    fetch(url, {
      headers: {
        accessToken: getCookie("accessToken"),
      },
    })
      .then((response) => {
        if (response.bodyUsed) {
          console.log("");
        } else if (response.ok) {
          return response.json();
        } else {
          console.log("ELSE");
        }
      })
      .then((result) => {
        console.log(result.responseData);
      })
      .catch((err) => {
        console.log("ERROR");
      });
  }, []);

  return (
    <div className="">
      <div className="Left">
        <div>
          <img
            src={
              userInformation.profileImage === "" ||
              userInformation.profileImage === "none"
                ? "#"
                : userInformation.profileImage
            }
            alt="#"
          />
          <ul>
            <li>{userInformation.nickName}</li>
            <li>{userInformation.description}</li>
          </ul>
        </div>
        <ul>
          <li>{userInformation.point} </li>
          <li><Link to="/PointAdd"></Link></li>
          <li><Link to="./PointRefund"></Link></li>
          <li><Link to="./PointUsed"></Link></li>
        </ul>
        <ul>
          <li>Category</li>
          <li>  </li>
          <li> [group_name] </li>
        </ul>
      </div>
      <div className="Right">
        <div className="Up">
          <div className="subTitle"> </div>
          <div className="studyLeft"></div>
          <div className="studyCenter">
            <ul>
              <li> </li>
              <li>  </li>
              <li>  </li>
              <li> </li>
            </ul>
            <ul>
              <li>
                {parseInt(userInformation.studyTimeToday / 60)
                  ? parseInt(userInformation.studyTimeToday / 60) + ""
                  : ""}{" "}
                {userInformation.studyTimeToday % 60
                  ? (userInformation.studyTimeToday % 60) + ""
                  : ""}
                {userInformation.studyTimeToday % 60 === "" ||
                userInformation.studyTimeToday % 60 === 0
                  ? "0"
                  : ""}
              </li>
              <li>
                {parseInt(userInformation.studyTimeWeek / 60)
                  ? parseInt(userInformation.studyTimeWeek / 60) + ""
                  : ""}{" "}
                {userInformation.studyTimeWeek % 60
                  ? (userInformation.studyTimeWeek % 60) + ""
                  : ""}
              </li>
              <li>
                {parseInt(userInformation.studyTimeMonth / 60)
                  ? parseInt(userInformation.studyTimeMonth / 60) + ""
                  : ""}{" "}
                {userInformation.studyTimeMonth % 60
                  ? (userInformation.studyTimeMonth % 60) + ""
                  : ""}
              </li>
              <li>
                {parseInt(userInformation.studyTimeTotal / 60)
                  ? parseInt(userInformation.studyTimeTotal / 60) + ""
                  : ""}{" "}
                {userInformation.studyTimeTotal % 60
                  ? (userInformation.studyTimeTotal % 60) + ""
                  : ""}
              </li>
            </ul>
          </div>
        </div>
        <div className="studyRight"></div>
        <div className="Down">
          <div className="subTitle"> </div>

          <div className="studyLeft">
            <ul>
              <li></li>
              <li></li>
              <li> OFF</li>
              <li> </li>
              <li> OFF</li>
              <li></li>
            </ul>
            <ul>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
          <div className="studyCenter"></div>
          <div className="studyRight">
            <ul>
              <li>MyPage</li>
              <li><Link to ="./PointAdd"> </Link></li>
              <li> </li>
              <li> </li>
              <li>FAQ</li>
              <li></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}

export default MyPage;
